package org.example.eparish.pages

import androidx.compose.runtime.Composable
import com.varabyte.kobweb.compose.css.autoLength
import com.varabyte.kobweb.compose.css.functions.linearGradient
import com.varabyte.kobweb.compose.foundation.layout.Box
import com.varabyte.kobweb.compose.foundation.layout.Column
import com.varabyte.kobweb.compose.foundation.layout.Row
import com.varabyte.kobweb.compose.ui.Alignment
import com.varabyte.kobweb.compose.ui.Modifier
import com.varabyte.kobweb.compose.ui.modifiers.*
import com.varabyte.kobweb.compose.ui.toAttrs
import com.varabyte.kobweb.core.Page
import com.varabyte.kobweb.framework.annotations.DelicateApi
import com.varabyte.kobweb.silk.components.graphics.Image
import com.varabyte.kobweb.silk.components.icons.fa.FaBars
import com.varabyte.kobweb.silk.components.icons.fa.FaXmark
import com.varabyte.kobweb.silk.components.icons.fa.IconSize
import com.varabyte.kobweb.silk.style.breakpoint.Breakpoint
import com.varabyte.kobweb.silk.theme.breakpoint.rememberBreakpoint
import org.example.eparish.components.Global
import org.example.eparish.components.eparishColors
import org.jetbrains.compose.web.css.Color
import org.jetbrains.compose.web.css.percent
import org.jetbrains.compose.web.css.px
import org.jetbrains.compose.web.css.vh
import org.jetbrains.compose.web.dom.Button
import org.jetbrains.compose.web.dom.Span

@OptIn(DelicateApi::class)
@Page("/home")
@Composable
fun Home() {

    // Detect screen size
    val isLargeScreen = rememberBreakpoint() >= Breakpoint.LG

    Box(
        modifier = Modifier
            .fillMaxSize()
            .backgroundImage(
                linearGradient(
                    eparishColors.secondary,
                    eparishColors.third
                )
            ),
        contentAlignment = Alignment.TopStart
    ) {

        Column(
            modifier = Modifier
                .fillMaxWidth()
                .maxWidth(1300.px)
                .margin(leftRight = autoLength)
        ) {

            // --- TOP BAR ---
            Row(
                modifier = Modifier
                    .fillMaxWidth()
                    .height(64.px)
                    .borderRadius(12.px)
                    .padding(topBottom = 8.px, leftRight = 16.px)
                    .margin(top = 12.px, bottom = 12.px)
                    .backgroundImage(
                        linearGradient(
                            eparishColors.primary,
                            eparishColors.third
                        )
                    ),
                verticalAlignment = Alignment.CenterVertically
            ) {

                // Hamburger shows only on MEDIUM + SMALL screens
                if (!isLargeScreen) {
                    Button(
                        attrs = Modifier
                            .padding(10.px)
                            .background(Color.transparent)
                            .borderRadius(50.px)
                            .border(0.px)
                            .onClick { Global.showNav.value = !Global.showNav.value }
                            .toAttrs()
                    ) {
                        if (Global.showNav.value) {
                            FaXmark(size = IconSize.LG, modifier = Modifier.color(Color.white))
                        } else {
                            FaBars(size = IconSize.LG, modifier = Modifier.color(Color.white))
                        }
                    }
                }

                Span(attrs = Modifier.size(16.px).toAttrs())

                Image(
                    src = "logo.png",
                    modifier = Modifier.size(60.px)
                )
            }

            // --- MAIN RESPONSIVE AREA ---
            if (isLargeScreen) {
                // DESKTOP MODE (Sidebar left, content right)
                Row(
                    modifier = Modifier.fillMaxSize(),
                    verticalAlignment = Alignment.Top
                ) {

                    // Sidebar always visible on large screens
                    Box(
                        modifier = Modifier
                            .height(100.vh)
                            .width(25.percent)
                            .minWidth(200.px)
                            .padding(16.px)
                    ) {
                        eParishNavigation()
                    }

                    // Main content
                    Box(
                        modifier = Modifier
                            .fillMaxSize()
                            .padding(16.px)
                    ) {
                        RenderContent()
                    }
                }

            } else {
                // MOBILE / TABLET MODE (Sidebar above content)
                Column(
                    modifier = Modifier.fillMaxWidth(),
                    horizontalAlignment = Alignment.Start
                ) {

                    // Sidebar only when toggled
                    if (Global.showNav.value) {
                        Box(
                            modifier = Modifier
                                .fillMaxWidth()
                                .padding(16.px)
                                .background(eparishColors.text.copy(alpha = 0.1f.toInt()))
                                .borderRadius(12.px)
                        ) {
                            eParishNavigation()
                        }
                    }

                    // Main content below
                    Box(
                        modifier = Modifier
                            .fillMaxWidth()
                            .padding(16.px)
                    ) {
                        RenderContent()
                    }
                }
            }
        }
    }
}
@Composable
fun RenderContent() {
    when (Global.selectedIndex.intValue) {
        0 -> Dashboard()
        1 -> Catholics()
        2 -> Leaders()
        3 -> Statistics()
        else -> Dashboard()
    }
}
